.root {
  position: relative;
  right: 100px;
  @media (max-width: 1260px) {
    right: 0px;
  }
  @media (max-width: 580px) {
    margin-bottom: 10px;
    margin-left: 10px;
  }
}

.input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 12px 20px;
  padding-left: 45px;
  margin-right: 15px;
  width: 400px;
  border-radius: 10px;
  font-size: 16px;
  @media (max-width: 1030px) {
    width: 250px;
  }
  @media (max-width: 850px) {
    width: 150px;
  }
  &:focus {
    border: 1px solid rgba(0, 0, 0, 0.25);
  }
}

.icon {
  width: 24px;
  height: 24px;
  opacity: 0.5;
  position: absolute;
  left: 12px;
  top: 12px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}

.clearIcon {
  width: 14px;
  height: 14px;
  opacity: 0.4;
  position: absolute;
  right: 28px;
  top: 16px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}
